<template>
	<div :class="topState ? 'top topChange' : 'top'">
			<YiDuNav :list="list" :logo="logo" :call="callback" backgroundColor="transparent" class="yidunav"></YiDuNav>
	</div>
</template>

<script>
import YiDuNav from '@/components/nav/YiDuNav.vue';
export default {
	components: { YiDuNav },
	data() {
		return {
			list: [
				{
					text: '首页',
					type:'text'
				},
				{
					text: '案例',
					type:'text'
				},
				{
					text: '优势',
					type:'text'
				}
				,
				{
					text: '服务',
					type:'text'
				},
				{
					text: '免费发布需求',
					type:'button'
				}
			],
			logo:{
				url:'http://macosx.online/wp-content/uploads/2020/06/矩形-42@2x.png',
				width:'68px',
				height:'30px'
			},
			activationItem: null,
			timer: null,
			topState: false
		};
	}
	,
	mounted: function() {
		window.addEventListener('scroll', this.handleScroll, true); // 监听（绑定）滚轮滚动事件
	},
	methods: {
		callback(item) {
			console.log(item);
		},
		handleScroll: function() {
			let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
			console.log(scrollTop);
			//滚动条到底部的条件
			if (scrollTop > 100) {
				// div 到头部的距离 + 屏幕高度 = 可滚动的总高度
				this.topState = true;
			} else {
				this.topState = false;
			}
		}
	},
	destroyed: function() {
		window.removeEventListener('scroll', this.handleScroll); //  离开页面清除（移除）滚轮滚动事件
	}
};
</script>

<style>
.top {
	width: 100%;
	height: 2.75rem;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 100;
	background:rgba(22,45,64,0.56);
}
.topChange {
	background-color: #03192b;
	opacity: 1;
}

</style>
