<template>
		<div class="yidunav" :style="{ 'background-color': backgroundColor, color: textColor }">
			<div class="logo" v-if="logo"><el-image :style="{ width: logo.width, height: logo.height, 'margin-left': '10%' }" :src="logo.url" fit="fill"></el-image></div>
			<div style="float: right;margin-right:10%;">
				<!--没有溢出的时候-->
				<div v-if="!isOver">
					<a v-for="(item, i) in list" class="nav" :key="i">
						<!--普通导航-->
						<div v-if="!item.childrens && item.type == 'text'" @click="nav_click(item)">
							<span :class="item == activationItem ? 'text textAct' : 'text'">{{ item.text }}</span>
							<span v-if="item.badge" class="badge">{{ item.badge }}</span>
							<div class="navbox" v-if="item == activationItem">
								<span class="n1 n_animation" v-if="count > 0"></span>
								<span class="n2 n_animation" v-if="count > 1"></span>
								<span class="n3 n_animation" v-if="count > 2"></span>
								<span class="n4 n_animation" v-if="count > 3"></span>
							</div>
						</div>

						<!--导航下拉列表-->
						<el-dropdown v-if="item.childrens && item.type == 'text'">
							<span class="text">
								{{ item.text }}
								<i class="el-icon-arrow-down el-icon--right"></i>
							</span>
							<el-dropdown-menu slot="dropdown" class="children_dropdown">
								<el-dropdown-item v-for="(children, k) in item.childrens" :key="k">
									<span @click="children_click(children)">{{ children.text }}</span>
								</el-dropdown-item>
							</el-dropdown-menu>
						</el-dropdown>
						<!--导航类型为按钮时-->
						<div v-if="item.type == 'button'" @click="button_click(item)" class="button" style="margin-top: -0.4rem;">{{ item.text }}</div>
					</a>
				</div>

				<!--溢出的时候-->
				<div v-if="isOver">
					<!--导航下拉列表-->
					<el-dropdown trigger="click">
						<i class="menu_icon" @click="menu_click">
							<span></span>
							<span></span>
							<span></span>
						</i>
						<el-dropdown-menu slot="dropdown" class="children_dropdown">
							<el-dropdown-item v-for="(item, key) in list" :key="key">
								<div @click="nav_click(item)" v-if="!item.childrens && item.type == 'text'">
									{{ item.text }}
									<span v-if="item.badge" class="over_badge">{{ item.badge }}</span>
								</div>
								<div v-if="item.childrens && item.type == 'text'">
									{{ item.text }}
									<i class="el-icon-arrow-down el-icon--right"></i>
									<div class="childNav">
										<div class="childItem" v-for="(child, ck) in item.childrens" :key="ck" @click="children_click(child)">{{ child.text }}</div>
									</div>
								</div>
								<div v-if="item.type == 'button'" @click="button_click(item)" class="button">{{ item.text }}</div>
							</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</div>
			</div>
		</div>
</template>

<script>
export default {
	props: {
		backgroundColor: {
			type: String,
			default: '#1b1f23'
		},
		textColor: {
			type: String,
			default: 'white'
		},
		call: {
			type: Function,
			default: function(item) {
				console.log(item);
			}
		},
		list: {
			type: Array,
			default: function() {
				return [{ text: '首页', activation: true, count: 1 }];
			}
		},
		logo: {
			type: Object,
			default: function() {
				return undefined;
			}
		}
	},
	data() {
		return {
			activationItem: null,
			timer: null,
			count: 1,
			screenWidth: 1000,
			isOver: false,
			showOverMenu: true,
			drawer: false,
			direction: 'ltr',
			clickItem: {}
		};
	},
	mounted() {
		if (this.list.length > 0) {
			this.nav_click(this.list[0]);
		}
		const that = this;
		window.onresize = () => {
			return (() => {
				window.screenWidth = window.innerWidth;
				that.screenWidth = window.screenWidth;
			})();
		};
		that.check_over();
	},
	methods: {
		n_animation() {
			if (this.activationItem == null) {
				return;
			}
			if (this.count > 4) {
				clearInterval(this.timer);
				return;
			}
			this.count++;
		},
		check_over() {
			if (this._isMobile()) {
				this.isOver = true;
				return;
			}
			let minWidth = this.list.length * 150;
			let diff = this.screenWidth - minWidth;
			if (diff < 0) {
				this.isOver = true;
			} else {
				this.isOver = false;
			}
		},
		nav_click(item) {
			if (this.activationItem != null) {
				if (item == this.activationItem) {
					return;
				} else {
					if (this.timer != null) {
						clearInterval(this.timer);
						this.timer = null;
					}
					this.activationItem = item;
				}
			} else {
				this.activationItem = item;
			}
			this.count = 1;
			this.call(item);
			this.timer = setInterval(this.n_animation, 100);
		},
		children_click(children) {
			this.call(children);
		},
		button_click(children) {
			this.call(children);
		},
		_isMobile() {
			let flag = navigator.userAgent.match(/(phone|pod|iPhone|iPod|Android|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
			return flag;
		},
		menu_click() {
			this.drawer = !this.drawer;
		},
		handleClose() {
			this.drawer = false;
		}
	},
	watch: {
		screenWidth(val) {
			this.screenWidth = val;
			this.check_over();
		}
	},
	created() {
		this.screenWidth = document.body.scrollWidth;
		this.check_over();
	},
	beforeUpdate() {
		this.check_over();
	}
};
</script>

